  border-left: 5px solid rgb(39, 113, 217);
  padding: 0 5px;
  margin-bottom: 5px;
}
.el-date-editor.el-input,
.el-date-editor.el-input__inner {
  width: 100%;
}
.el-select {
  width: 100%;
}
.room-div{
    background-color:#fff; 
    padding: 3px 20px 20px 20px;
}
.room{
    display: flex;
}
.room-item {
  background-color: #edf4f9;
  padding: 8px;
}
.room-item img{
    width: 20px;
}
.room-item div{
    font-size: 12px;
}
.states{
    text-align: center;
    height: 35px;
    line-height: 35px;
}
.states span{
    margin-right: 10px;
}
.states i{
       width: 12px;
    border-radius: 15px;
    display: inline-block;
    height: 12px;
    margin-right: 5px;
}
.unse{
     background-color:#e5e5e5;
}
.manse{
     background-color:#4AFF40;
}
.womense{
     background-color:#FFD640;
}
.alse{
    background-color: rgb(39, 113, 217);
}
.submit-btn{
    width: 300px;
    margin-top: 20px;
}
</style>
<script>
export default {
  data() {
    return {
      options: [
        {
          value: "YS",
          label: "上海杨思医院"
        },
        {
          value: "AD",
          label: "上海安达医院"
        }
      ],
      value: "",
      value1: "",
      value2: "",
      value3: "",
      index:0,
      bedurl:"../../assets/hui.png",
       albedurl:"../../assets/lan.png",
      bedIndex:0
    };
  },
  methods:{
selectPatient:function(name,index){
    this.value2=name;
     this.index=index;
},
selectBed:function(index){
this.bedIndex=index;
},
open:function(){
     this.$message({  message:'提交成功',  center: true,type:"success"});
}
  }
};
</script>


